body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
  background: #4b6cb7;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #182848, #4b6cb7);  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-gradient(linear, left top, right top, from(#182848), to(#4b6cb7));
  background: -webkit-linear-gradient(left, #182848, #4b6cb7);
  background: linear-gradient(to right, #182848, #4b6cb7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: #fff;
}
*{
  padding:0;
  margin: 0 auto;
}
.notesHeader, .notesBody, .notesFooter{
  background-color: rgba(0,0,0,.5);
  padding: 4vw;
  margin: 0 auto;
}
.notesBody{
  margin-top: 2vw;
}
.notesFooter{
  bottom:0;
  position: fixed;
  left: 0;
  width: 100%;
}
.Note{
    background-color: #fff;
    padding: 2vw;
    color: #000;
    margin-top: 1vw;
    border-radius: 5px;
}
.Note span{
    cursor: pointer;
    font-size: 6vw;
}
.NoteForm input[type='text']{
    padding: 3vw;
    width: 65vw;
    border-top-left-radius: 30vw;
    border-bottom-left-radius: 30vw;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
}
.NoteForm button{
    padding: 3vw;
    background-color: #333;
    border:0;
    color: #fff;
    border-top-left-radius: 0vw;
    border-bottom-left-radius: 0vw;
    border-bottom-right-radius: 30vw;
    border-top-right-radius: 30vw;
}
